* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body {
  max-width: 100vw;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
}

body {
  color: #000;
  background: #fff;
}

a {
  color: inherit;
  text-decoration: none;
}

@media (prefers-color-scheme: dark) {
  html {
    color-scheme: dark;
  }
  body {
    color: #fff;
    background: #000;
  }
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.btn {
  background: #0070f3;
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  transition: background 0.2s;
}

.btn:hover {
  background: #0051a2;
}

.btn:disabled {
  background: #ccc;
  cursor: not-allowed;
}

.btn-secondary {
  background: #666;
}

.btn-secondary:hover {
  background: #555;
}

.input {
  border: 2px solid #ddd;
  padding: 12px;
  border-radius: 8px;
  font-size: 16px;
  width: 100%;
  margin-bottom: 12px;
}

.input:focus {
  outline: none;
  border-color: #0070f3;
}

.chat-container {
  border: 2px solid #ddd;
  border-radius: 12px;
  height: 400px;
  overflow-y: auto;
  padding: 20px;
  margin-bottom: 20px;
}

.message {
  margin-bottom: 12px;
  padding: 8px 12px;
  border-radius: 8px;
}

.message.user {
  background: #e3f2fd;
  margin-left: 20px;
}

.message.assistant {
  background: #f5f5f5;
  margin-right: 20px;
}

.chat-input {
  display: flex;
  gap: 12px;
}

.status {
  padding: 8px 12px;
  border-radius: 8px;
  margin-bottom: 20px;
  text-align: center;
}

.status.connected {
  background: #e8f5e8;
  color: #2e7d32;
}

.status.disconnected {
  background: #ffebee;
  color: #c62828;
}

.status.connecting {
  background: #fff3e0;
  color: #f57c00;
}
